body {
  width: 100%;
  height: 100%;
  background-color: #F5F5F7;
}

.container {
  display: grid;
  margin: 0 20px;
  max-width: 1200px;
  min-height: 100vh;
  grid-template-rows: 70px 1fr 60px;
}

.header {
  padding: 10px 0;
  font-size: 40px;
  font-weight: 600;
  color: #1D1D1F;
  border-bottom: 1px solid #D2D2D7;
}

.content {
  display: grid;
  padding: 25px 0;
  align-content: flex-start;
  grid-gap: 25px;
}

.profile {
  display: grid;
  grid-auto-flow: row;
  grid-gap: 25px;
  justify-items: center;
}

.profile-desc {
  text-align: center;
}

.profile-desc__title {
  font-size: 20px;
  font-weight: 600;
}

.profile-desc__sub {
  font-size: 18px;
  font-weight: 300;
}

.links {
  display: grid;
  grid-auto-flow: row;
  grid-gap: 15px;
  width: 100%;
  max-width: 680px;
  justify-self: center;
}

.link {
  position: relative;
  display: grid;
  align-content: center;
  justify-content: center;
  height: 60px;
  border: 2px solid #D2D2D7;
  font-weight: 300;
  
  > img {
    position: absolute;
    top: 15px;
    left: 15px;
  }
  &:hover {
    background-color: #D2D2D7;
  }
}

.footer {
  padding-top: 15px;
  border-top: 1px solid #D2D2D7;
  font-size: 14px;
  font-weight: 300;
  color: #1D1D1F;
}

@media only screen and (min-width: 1240px) {
  .container {
    margin: 0 auto;
  }
  .content {
    padding: 50px 0;
  }
}